<script>
import { reactive } from "vue";
import { SlickList, SlickItem, HandleDirective } from "vue-slicksort";
import Sort from './components/Sort.vue'


function getHabitatFromHeaderImg(img) {
  let x  = img.substr(0, img.length-4).split('-');
  x = x.slice(x.length-2)
  x = x.map((e)=>{return e[0].toUpperCase() + e.substr(1)}).join(" ");
  return x;
}


export default {
  name: "App",
  components: {
    Sort,
  },
  props:{
    html: String,
  },
  data() {
    return {
      form: true,
      error: '',
      sortMode:'sort'
    }

  },

  methods: {
    go0: function() {
      console.log('hi');
      if(this.html.indexOf('html') < 0) {
        this.error = 'That is not HTML!'
      } else if(this.html.indexOf('<a href="https://dappervolk.com/register">Register</a>')>=0){
        this.error = 'You need to log in (this is the sign in page).'
      } else if($($.parseHTML(this.html)).find('.pets-pen').length == 0)  {
        this.error = 'This is not a menagerie'
      } else {
        this.form = false;
      }

    },
    go: function() {
      this.sortMode = 'sort';
      this.go0()
    },
    go2: function() {
      this.sortMode = 'daily';
      this.go0()
    }

  }
};
</script>

