<template>
  <div class="root">
    <div v-if="sortMode=='sort'">
      <h1>THERE IS NO SAVING RIGHT NOW. REFRESH AND ALL DATA IS LOST</h1>
    Pro tip: Try zooming out to get a better view of your entire menagerie!

    </div>
    <div v-else>
      Pro tip: Ctrl+Click to open the pet links in new tabs without leaving this window!
    </div>
    <SlickList
      v-model:list="menagerie.pens"
      @sort-end="sort"
      axis="xy"
      useWindowAsScrollContainer
      class="master-list"
      :useDragHandle="true"
    >
      <SlickItem
        v-for="(pen, index) in menagerie.pens"
        :index="index" :key="pen.id"
        class="list-item v-list-item" :item="pen"
        :disabled="sortMode != 'sort'"
        >
        <div
          v-handle class="handle"
        :style="{
          'background-color': habitats[pen.type].headerBackground,
          'color':  habitats[pen.type].headerText,
          }"

        >{{ pen.name }} ({{pen.pets.length}}/20)</div>
        <SlickList
          axis="xy"
          v-model:list="pen.pets"
          :group="'group10'"
          class="list"
          :style="{
            'background': 'linear-gradient(0deg, ' +habitats[pen.type].gradientFrom + ', ' + habitats[pen.type].gradientTo +' 95%) no-repeat',
            'color': 'white',
            }"
          @sort-end="sort"
          useWindowAsScrollContainer
          :useDragHandle="true"
        >
          <SlickItem v-for="(pet, index) in pen.pets"
             :index="index" :key="pet.id"
             class="list-item" :item="pet"
             :disabled="sortMode != 'sort'"
             >
             <div v-handle
             v-if="sortMode=='sort'"
              class="pet-handle"
             ></div>
             <div
             v-if="sortMode!='sort'"
             class="pet-actions">
               <a :href="'https://dappervolk.com/pet/pat/'+pet.id" target="PETPAT">
                 <div class="pet-pet pet-icon"></div></a>
               <a :href="'https://dappervolk.com/pet/chat/'+pet.id" target="PETCHAT">
                 <div class="pet-chat pet-icon"></div>
                 </a>
             </div>

            <img :src="pet.img" />
            <div class='pet-info'>
              <span class='pet-name'>{{ pet.name }}</span> <span class='pet-id'>(#{{pet.id}})</span>
            </div>
          </SlickItem>
        </SlickList>
      </SlickItem>
    </SlickList>

    <!-- <SlickList lockAxis="y" v-model="items" tag="ul">
        <SlickItem v-for="(item, index) in items" :index="index" :key="index" tag="li">
          {{ item }}
        </SlickItem>
      </SlickList> -->
  </div>
</template>

<script>
import { reactive } from "vue";
import { SlickList, SlickItem, HandleDirective } from "vue-slicksort";


function getHabitatFromHeaderImg(img) {
  let x  = img.substr(0, img.length-4).split('-');
  x = x.slice(x.length-2)
  x = x.map((e)=>{return e[0].toUpperCase() + e.substr(1)}).join(" ");
  return x;
}
export default {
  name: "Sort",
  components: {
    SlickList,
    SlickItem
  },
  data() {
    return {
      habitats: {
        "Verdant Fields":{
          "headerImage":"https://dappervolk.com/img/items_environment/0/5-verdant-fields.png",
          "headerBackground":"#7b913e",
          "headerText":"#f4e678",
          "gradientFrom":"#94b641",
          "gradientTo":"#a0d8ce",
          "profileTextColor":"#516a33"},"Mystic Swamp":{"headerImage":"https://dappervolk.com/img/items_environment/0/6-mystic-swamp.png","headerBackground":"#3d1e47","headerText":"#47a1a4","gradientFrom":"#438bb1","gradientTo":"#c2828c","profileTextColor":"#51376c"},"Golden Aquarium":{"headerImage":"https://dappervolk.com/img/items_environment/0/254-golden-aquarium.png","headerBackground":"#1a656c","headerText":"#ffba7e","gradientFrom":"#4b9da2","gradientTo":"#f4c48b","profileTextColor":"#1a656c"},"Coral Depths":{"headerImage":"https://dappervolk.com/img/items_environment/1/1404-coral-depths.png","headerBackground":"#3f1c5f","headerText":"#f6bd97","gradientFrom":"#437ea6","gradientTo":"#d87e8e","profileTextColor":"#3f1c5f"},"Starry Seaside":{"headerImage":"https://dappervolk.com/img/items_environment/0/199-starry-seaside.png","headerBackground":"#252255","headerText":"#f0cf8f","gradientFrom":"#395f94","gradientTo":"#6CC5A5","profileTextColor":"#83caca"},"Crystal Caves":{"headerImage":"https://dappervolk.com/img/items_environment/0/198-crystal-caves.png","headerBackground":"#453845","headerText":"#d8cda1","gradientFrom":"#95544b","gradientTo":"#ba8768","profileTextColor":"#d5b091"},"Jeweled Sky":{"headerImage":"https://dappervolk.com/img/items_environment/0/256-jeweled-sky.png","headerBackground":"#627da5","headerText":"#f5f4d0","gradientFrom":"#94bad0","gradientTo":"#f7cbaf","profileTextColor":"#3a5282"},"Obsidian Night":{"headerImage":"https://dappervolk.com/img/items_environment/0/255-obsidian-night.png","headerBackground":"#1d1f34","headerText":"#8acef1","gradientFrom":"#335784","gradientTo":"#86537a","profileTextColor":"#ffffff"}
          },
    }
  },

  directives: { handle: HandleDirective },
  props: {
    html: String,
    sortMode: String,
  },
  setup(props) {
    let $html = $($.parseHTML(props.html));
    let menagerie = reactive({
      username: $html.find('#menagerie div.toolbar > ul > li.active > a').text(),
      pens: [],
    })

    $html.find('.pets-pen').each((i, x) => {
      let pen = {
        id: $(x).data('pen'),
        pets: [], name: $(x).data('name'),
        type: getHabitatFromHeaderImg($(x).data('img'))};
      menagerie.pens.push(pen)
      $(x).find('.pet-li').each((j, y) => {
        pen.pets.push({
          id:$(y).find('a').attr('href').split('/').pop(),
          img: $(y).find('img').attr('src'),
          name: $(y).find('.pet-name').text()
        })
      });
    });
    console.log(menagerie)
    return {
      menagerie:menagerie, sortMode:props.sortMode
    };
  },
  methods: {
    sort({ newIndex, oldIndex, collection }) {
      console.log(newIndex, oldIndex, collection);
      //this.items[0].splice(newIndex, 0, this.items[0].splice(oldIndex, 1)[0]);
    },
    insert({ newIndex, value }) {
      console.log("Insert", newIndex, value);
    },
    update(newValue) {
      console.log("update", newValue);
    }
  }
};
</script>

<style>
.master-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.list {
  width: 750px;
  margin: 0 auto;
  padding: 0;
  padding-bottom: 20px;
  /* overflow: scroll; */

  background-color: #f3f3f3;
  border: 1px solid #efefef;
  border-radius: 3;
  text-align: left;
}

.list-item {
  position: relative;
  width: 25%;
  padding: 20px;

  box-sizing: border-box;
  -webkit-user-select: none;
  display: inline-block;
  vertical-align: top;
  height: auto;
}
.v-list-item {
  width: auto;
  display: flex;
  /* max-height: 785px; */
  min-height: 200px;
  padding: 0;
  margin: 2px;
  position: relative;
}

.handle {
  width: 45px;
  background: grey;
  writing-mode: vertical-rl;
  /*text-orientation: sideways;*/
  transform: scale(-1);

  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  text-transform: uppercase;
  color: white;
}
</style>

<style scoped>
.list-item img{
  width:100%;
}
.pet-info {
  position: absolute;
  bottom: 0;
  text-align: center;
  /* width: 100%; */
  left: 0;
  right: 0;
}
.pet-id {
  font-size: 0.5em;
  opacity:.7;
}
.pet-name {
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;
  font-weight: bold;
  font-family: "Roboto Condensed", "Helvetica", "Arial", sans-serif;
  color: #fff;
  margin: 10px auto 5px;
  vertical-align: middle;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: initial;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #f1f1f1;
  opacity: .5;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #fff;
}

.pet-handle {
  z-index:1000;
  position:absolute;top:0;left:0;bottom:0;right:0;
}

.pet-icon {
  vertical-align: middle;
  background-image: url(https://dappervolk.com/img/spritesheet.png);
  background-repeat: no-repeat;
  margin:0 auto;
}
.pet-actions {
  position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin:0 auto;
    opacity:0.5;
    font-size:2em;
    font-weight:bold;
}

.pet-actions:hover{
  opacity:1;
}
.pet-actions>a {
flex-grow:1;
text-align: center;
opacity:.8;
color:white;
display: flex;
align-items: center;
height:100%;
}
.pet-actions>a:hover{
  opacity:1;
  background:rgba(255,255,255,.5)

}
.pet-pet{
  background-position: -50px -225px;
  width: 35px;
  height: 45px;

}
.pet-chat{
  background-position: -100px -225px;
  width: 39px;
  height: 45px;
}
</style>